.button {
  position: relative;
  background-color: #c2ab81;
  width: 283px;
  height: 60px;

  transform: skew(-20deg);

  &:hover {
    background-color: rgba(#c2ab81, 0.8);
  }
}

.button a {
  position: relative;
  display: block;
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  height: 100%;
  padding: 19px 5px;
  color: white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  box-sizing: border-box;
}

.button a::after {
  position: absolute;
  content: '';
  top: 0;
  right: -15px;
  width: 10px;
  height: 100%;
  background-color: #ffffff;
}

.button:hover a::after {
  background-color: purple;
}
